.input-dropdown {
  color: rgb(100,100,100);
  background: rgb(245,245,245);
  cursor: pointer;

  /* .transaction-modal-input[disabled] */
  background: none;
  border: none;
  outline: none;
  transition: color 0.2s, background-color 0.2s;

  /* Bootstrap .form-control */
  width: 100%;
  height: 2.5rem;
  padding: .375rem .75rem;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #212529;
  border-radius: .25rem;
}

.input-dropdown:hover {
  /* .transaction-modal-input[disabled]:hover */
  color: rgb(100,100,100);
  background: rgb(245,245,245);
  cursor: pointer;
}

.input-dropdown-input {
  padding: 0;
}
